.profile-content{
    background-color: var(--darkBlue);
    width: 90%;
    border-radius: 20px;
}
.profile-identifiy{
    background-color: var(--Blue);
   border-radius: 20px;
   width: 100%;
   padding: 50px 40px;
}
.profile-photo{
    border: 3px solid white;
    width: 76px;
    border-radius: 50%;
    margin-bottom: 10%;
}
.profile-photo img{
    width: 70px;
    text-align: center;
}
.profile-name p{
  margin-bottom: 0px;
  font-size: 12px;
  color:var(--light);
}
.profile-name h3{
    color: var(--light);
    letter-spacing: 1px;
    font-size: 30px;
}
.profile-side-bar{
padding: 15px 0px;
}
.menu{
    list-style: none;
}

@media screen and (max-width: 1034px){
    .profile-content{
        width: 30%;
    }
}
@media screen and (max-width: 800px){
    .profile-content{
        width: 100%;
    }
}
@media screen and (max-width:668px){
    .menu{
       display: flex;
       flex-direction: row;
       justify-content: space-evenly;
       align-items: center;
       padding-left: 0px !important;
    }
    .profile-side-bar{
        padding: 10px 0px 0px 0px;
    }
}